@media (max-width: 768px) {
  .tag-list li {
    margin-right: 16px; } }

@media (max-width: 375px) {
  .tag-list li {
    margin-right: 8px; } }

.blogInfo {
  display: flex;
  justify-content: space-between; }

.blogInfo-content {
  max-width: 825px; }

.blogInfo-sidebar {
  max-width: 255px; }

@media (max-width: 768px) {
  .blogInfo-content {
    max-width: 510px; }
  .blogInfo-sidebar {
    max-width: 150px; } }

@media (max-width: 375px) {
  .blogInfo {
    display: block; }
  .blogInfo-sidebar {
    max-width: 100%; }
  .blogInfo-content {
    margin-bottom: 30px; } }

.breadcrumb {
  display: flex; }

.breadcrumb li:not(:first-child)::before {
  content: "/";
  padding: 0 4px; }

.breadcrumb .active {
  color: #AA0601; }

.blogMenu .container {
  padding-top: 0px;
  padding-bottom: 0px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.blogMenu-list {
  display: flex; }

.blogMenu-item a {
  display: block;
  padding: 12px 0;
  margin-right: 48px; }

@media (max-width: 768px) {
  .blogMenu-item a {
    padding: 16px 0;
    margin-right: 32px; } }

@media (max-width: 375px) {
  .blogMenu-item a {
    margin-right: 0px; }
  .blogMenu .container {
    display: block; }
  .blogMenu-list {
    justify-content: space-between; }
  .blogMenu-search {
    display: none; } }
